body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 5%;

}

.col-md-4 {
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;

}

.card {
    padding: 0%;
    height: 480px;
    margin-top: 30px;
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.card:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);

}
.card .image {
    height: 300px;
    padding: 20px;
    background: #fff;
    margin: auto;
}
.header {
    border-top: .5px solid #d3d3d3;
    width: 100%;
    padding-top: 5%;
    height: 70px;
    overflow: hidden;
    font-weight: 400;
}

.price {
    height: 20px;
    
}

.category {
    font-weight: 400;
}
.cardd {
    padding: 0%;
    width: 70%;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid #dedede
}

.no-gutters div img {
    height: 400px;
    padding: 15% 10%;
    border-right: 1px solid #dedede;
}

.col-md-8 {
    padding-top: 3%;
}

.tag {
    display: inline-block;
    width: auto;
    height: 38px;
    margin-top: 1%;
    background-color: #5ca08e;
    border-radius: 3px 4px 4px 3px;
    border-left: 1px solid #5ca08e;
    margin-left: 19px;
    position: relative;
    color: white;
    font-weight: 300;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 22px;
    line-height: 38px;
    padding: 0 10px 0 10px;
}

.tag:before {
    content: "";
    position: absolute;
    display: block;
    left: -19px;
    width: 0;
    height: 0;
    border-top: 19px solid transparent;
    border-bottom: 19px solid transparent;
    border-right: 19px solid #5ca08e;
}

.tag:after {
    content: "";
    background-color: white;
    border-radius: 50%;
    width: 4px;
    height: 4px;
    display: block;
    position: absolute;
    left: -9px;
    top: 17px;
}
button{
    border: none;
    background-color: rgb(238, 10, 124);
    color:white;
    padding:1% 4%;
    border-radius: 5px;
}
